import React from 'react';
import { render, screen, fireEvent } from '@testing-library/react-native';
import HeroContinueSection from '../../../../src/screens/Dashboard/components/HeroContinueSection';

// Mock des dépendances
jest.mock('expo-linear-gradient', () => ({
  LinearGradient: ({ children, ...props }) => {
    const { View } = require('react-native');
    return <View testID="linear-gradient" {...props}>{children}</View>;
  }
}));

jest.mock('../../../../src/utils/constants', () => ({
  LANGUAGE_LEVELS: {
    '1': { title: 'Débutant', color: '#10B981' },
    '2': { title: 'Élémentaire', color: '#3B82F6' },
    '3': { title: 'Intermédiaire', color: '#8B5CF6' }
  }
}));

describe('HeroContinueSection', () => {
  const mockOnPress = jest.fn();

  const defaultProps = {
    lastActivity: null,
    isLoading: false,
    onPress: mockOnPress,
    accentColor: '#3B82F6'
  };

  const renderComponent = (props = {}) => {
    return render(
      <HeroContinueSection {...defaultProps} {...props} />
    );
  };

  beforeEach(() => {
    jest.clearAllMocks();
  });

  describe('Sans activité récente', () => {
    it('devrait afficher le bouton "Commencer"', () => {
      renderComponent();
      
      expect(screen.getByText('Commencer')).toBeTruthy();
      expect(screen.getByText('Choisissez votre niveau et commencez à apprendre')).toBeTruthy();
    });

    it('devrait appeler onPress avec "levelSelection" quand on clique sur Commencer', () => {
      renderComponent();
      
      const button = screen.getByText('Commencer');
      fireEvent.press(button);
      
      expect(mockOnPress).toHaveBeenCalledWith('levelSelection');
    });
  });

  describe('Avec activité récente', () => {
    const lastActivity = {
      type: 'vocabulary',
      level: '2',
      title: 'Vocabulaire Famille',
      timeElapsed: 'Il y a 5 min'
    };

    it('devrait afficher le bouton "Continuer"', () => {
      renderComponent({ lastActivity });
      
      expect(screen.getByText('Continuer')).toBeTruthy();
      expect(screen.getByText('Vocabulaire Famille')).toBeTruthy();
      expect(screen.getByText('Il y a 5 min')).toBeTruthy();
    });

    it('devrait appeler onPress avec l\'activité quand on clique sur Continuer', () => {
      renderComponent({ lastActivity });
      
      const button = screen.getByText('Continuer');
      fireEvent.press(button);
      
      expect(mockOnPress).toHaveBeenCalledWith(lastActivity);
    });

    it('devrait gérer une activité sans titre', () => {
      const activityWithoutTitle = { ...lastActivity, title: undefined };
      renderComponent({ lastActivity: activityWithoutTitle });
      
      expect(screen.getByText('Continuer')).toBeTruthy();
    });

    it('devrait gérer une activité sans temps écoulé', () => {
      const activityWithoutTime = { ...lastActivity, timeElapsed: undefined };
      renderComponent({ lastActivity: activityWithoutTime });
      
      expect(screen.getByText('Continuer')).toBeTruthy();
      expect(screen.getByText('Vocabulaire Famille')).toBeTruthy();
    });
  });

  describe('État de chargement', () => {
    it('devrait afficher un indicateur de chargement', () => {
      renderComponent({ isLoading: true });
      
      expect(screen.getByText('Chargement...')).toBeTruthy();
    });

    it('ne devrait pas afficher de bouton pendant le chargement', () => {
      renderComponent({ isLoading: true });
      
      expect(screen.queryByText('Commencer')).toBeNull();
      expect(screen.queryByText('Continuer')).toBeNull();
    });
  });

  describe('Gestion des erreurs', () => {
    it('devrait fonctionner sans onPress', () => {
      renderComponent({ onPress: undefined });
      
      expect(screen.getByText('Commencer')).toBeTruthy();
    });

    it('devrait fonctionner sans accentColor', () => {
      renderComponent({ accentColor: undefined });
      
      expect(screen.getByText('Commencer')).toBeTruthy();
    });
  });
});